import logo from './logo.svg';
import './App.css';
var CryptoJS = require("crypto-js");

function App() {
  var AES = {
    encript: function encript(){
    document.getElementsByName("AESENC")[0].value = CryptoJS.AES.encrypt(document.getElementsByName("AESMENSA")[0].value, 
    document.getElementsByName("frase")[0].value);

  },
            desencript: function desenc(){
    document.getElementsByName("AESMENSA")[0].value = CryptoJS.AES.decrypt(document.getElementsByName("AESENC")[0].value, 
              document.getElementsByName("frase")[0].value);
            }}
  var SHA2 = { encript: function encript(){
    document.getElementsByName("SHA2ENC")[0].value = CryptoJS.SHA256(document.getElementsByName("SHA2MEN")[0].value);

  },
            desencript: function desenc(){
    document.getElementsByName("SHA256MEN")[0].value = CryptoJS.SHA256.decrypt(document.getElementsByName("SHA256ENC")[0].value);
            }}
  return (
    <div className="App">
      <h2>AES</h2>
      <div className="first">
          <textarea placeholder="mensagem" name="AESMENSA" ></textarea>
          <textarea placeholder="frase secreta" name="frase"></textarea>

          <div>
            <button onClick={()=>AES.encript()}>encriptar</button>
            <button onClick={()=>AES.desencript()}>desencriptar</button>
          </div>
          <textarea placeholder="mensagem encriptada" name="AESENC"></textarea>
      </div>
      <h2>SHA-2</h2>

      <div className="first">
      <textarea placeholder="mensagem" name="SHA2MEN" ></textarea>
          <div>
           <button onClick={()=>SHA2.encript()}>encriptar</button>
            <button onClick={()=>SHA2.desencript()}>desencriptar</button>
          </div>
          <textarea placeholder="mensagem encriptada"name="SHA2ENC" ></textarea>
      </div>
    </div>
  );
}

export default App;
